.write-article input {
    border-radius: 5px !important;
    border: none;
    color: #757474;
    width: 100%;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
}
.write-article .show-name-checkbox {
    cursor: pointer;
    margin-top: 5px;
}
.write-article .show-name-checkbox input {
    width: 14px;
    height: 14px;
}
.write-article .show-name-checkbox label {
    font-size: 90%;
}
.form-control .react-tags__search-input {
    border-radius: 5px !important;
    border: none;
    color: #757474;
}
.form-group span {
    font-size: 10px;
    color: red;
    text-align: right;
}


